@font-face {
  font-family: Gallient;
  src: url("./fonts/Gallient.woff") format("woff");
  font-weight: 400;
}

@import '../styles/history.scss';
@import "contacts.scss";

html {
  overflow-x: hidden;
}

body {
  background: #000000;
  -webkit-font-smoothing: antialiased;
  color: #FFF;
}

input{
  color: white;
}

body:not(.loaded) {
  overflow: hidden;
}

.wrapper {
  width: 100vw;
  margin: 0 auto;
  padding: 0 2.604vw;
}

header {
  left: 0;
  top: 0;
  top: 1.302vw;
  z-index: 5;
  padding: 1.302vw 0;
  position: absolute;

  .wrapper {
    display: flex;
    justify-content: space-between;
    position: relative;
  }

  .header_logo {
    width: 2.344vw;
    height: 2.656vw;

    img {
      width: 100%;
      height: auto;
    }
  }

  nav {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    gap: 3.646vw;
    overflow: hidden;
  }
}

.header_nav-link {
  color: #FFF;
  font-family: Manrope;
  font-size: 0.833vw;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: -0.033vw;
  text-transform: uppercase;
  position: relative;

  &:hover::before {
    transform-origin: 100% 50%;
    transform: scale3d(1, 1, 1);
  }

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 0.052vw;
    bottom: 0;
    right: 0;
    pointer-events: none;
    transform-origin: 0% 50%;
    transform: scale3d(0, 1, 1);
    transition: transform 0.3s;
    background: white;
  }
}

.hero {
  padding-top: 8.333vw;
  padding-bottom: 7.292vw;
  height: calc(100vh + 4.688vw);

  .wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    height: 100%;
  }
}

.hero_title {
  color: #FFF;
  font-family: Manrope;
  font-size: 6.667vw;
  font-style: normal;
  font-weight: 500;
  line-height: 96%;
  letter-spacing: -0.4vw;
  text-transform: uppercase;
  text-align: center;
  transform-style: preserve-3d;

  .gallient {
    font-family: Gallient;
    font-size: 5.938vw;
    font-weight: 400;
    line-height: 100%;
    letter-spacing: -0.087vw;
    margin-right: 0.938vw;
    transform-style: preserve-3d;
  }
}

.hero_title,
.hero_title .gallient {
  perspective: 65.104vw;
}

.hero-bottom {
  display: flex;
  justify-content: space-between;
  gap: 1.25vw;
  width: 100%;

  .button-arrow {
    margin-top: 1.25vw;
    height: 0;
  }
}

.hero-bottom_content {
  display: flex;
  flex-direction: column;
  gap: 0.521vw;
}

.hero-bottom_title {
  color: #FFF;
  font-family: Manrope;
  font-size: 1.458vw;
  font-style: normal;
  font-weight: 500;
  line-height: 110%;
  letter-spacing: -0.058vw;
  text-transform: uppercase;
}

.hero-bottom_subtitle {
  width: 23.958vw;
  color: #707070;
  font-family: Manrope;
  font-size: 0.938vw;
  font-style: normal;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: -0.037vw;
}

.button-arrow {
  display: flex;
  gap: 0.208vw;
  height: 3.177vw;
  overflow: hidden;
  width: fit-content;

  .button-content {
    border-radius: 0.104vw;
    border: 0.052vw solid #FFF;
    padding: 0vw 2.24vw;
    height: 100%;
    transition: .3s;

    span {
      display: none;
    }

    p {
      color: #FFF;
      font-family: Manrope;
      font-size: 0.938vw;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      letter-spacing: -0.037vw;
      text-transform: uppercase;
      margin-top: 0.885vw;
      transition: 0.3s;
    }
  }

  .button-side {
    border-radius: 0.104vw;
    border: 0.052vw solid #FFF;
    background: #FFF;
    position: relative;
    width: 3.177vw;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;

    &:after {
      content: "";
      width: 0.99vw;
      height: 0.99vw;
      top: 1.042vw;
      left: 1.094vw;
      position: absolute;
      background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2.5 17.5L16.5 3.5M16.5 3.5L16.5 17.5M16.5 3.5L2.5 3.5' stroke='black' stroke-width='2'/%3E%3Crect x='0.374023' y='2.4939' width='2.92454' height='2.92454' transform='rotate(-45 0.374023 2.4939)' fill='white'/%3E%3Crect x='15.4404' y='17.5625' width='2.92454' height='2.92454' transform='rotate(-45 15.4404 17.5625)' fill='white'/%3E%3C/svg%3E%0A");
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      transition: .3s ease-in-out;
    }
  }

  &:hover {
    .button-content {
      background: white;

      p {
        color: black;
      }
    }

    .button-side:after {
      transform: rotate(45deg);
    }
  }
}

.advantages {
  padding: 7.292vw 0;

  .wrapper {
    width: 78.854vw;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 20.833vw;
  }
}

.advantages-card {
  width: 22.917vw;
  min-height: 22.917vw;
  background: #0E0E0E;
  padding: 1.042vw;
  display: flex;
  flex-direction: column;
  gap: 1.25vw;
  justify-content: space-between;
  position: relative;

  &:nth-child(even) {
    margin-left: auto;
  }

  &:nth-child(odd) {
    margin-right: auto;
  }

  &:after {
    content: "";
    position: absolute;
    left: 1.042vw;
    bottom: 1.042vw;
    width: 1.302vw;
    height: 1.302vw;
    background-image: url("data:image/svg+xml,%3Csvg width='25' height='25' viewBox='0 0 25 25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15.5814 7.79056L21.3953 3.37196L25 9.7673L18.2558 12.558L25 15.6975L21.2791 21.9766L15.4651 17.4417L16.2791 24.9999H8.72093L9.53488 17.4417L3.72093 21.9766L0 15.6975L6.62791 12.558L0 9.7673L3.60465 3.37196L9.4186 7.79056L8.72093 0.232422H16.2791L15.5814 7.79056Z' fill='white'/%3E%3C/svg%3E%0A");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    transition: .3s;
  }

  &:nth-child(1) .advantages-card_text {
    max-width: calc(10.885vw + 2.083vw);
  }

  &:nth-child(2) .advantages-card_text {
    max-width: calc(14.635vw + 2.083vw);
  }

  &:nth-child(3) .advantages-card_text {
    max-width: calc(13.49vw + 2.083vw);
  }

  &:nth-child(4) .advantages-card_text {
    max-width: calc(13.698vw + 2.083vw);
  }

}

.advantages-card_title {
  color: #FFF;
  font-family: Manrope;
  font-size: 1.875vw;
  font-style: normal;
  font-weight: 600;
  line-height: 100%; /* 1.875vw */
  letter-spacing: -0.094vw;
  text-transform: uppercase;
}

.advantages-card_text {
  padding-left: 2.083vw;
  margin-left: auto;
}

.text-16 {
  color: #808080;
  font-family: Manrope;
  font-size: 0.833vw;
  font-style: normal;
  font-weight: 500;
  line-height: 125%;
  letter-spacing: -0.025vw;
}

.presentation {
  display: flex;
  flex-direction: column;
  gap: 4.167vw;
  padding: 7.292vw 0;

  .presentation-header {
    display: flex;
    gap: 2.083vw;
    justify-content: space-between;
  }

  .presentation-text {
    color: #404040;
    font-family: Manrope;
    font-size: 2.5vw;
    font-style: normal;
    font-weight: 500;
    line-height: 105%;
    letter-spacing: -0.125vw;
    text-transform: uppercase;
    width: 44.688vw;
  }

  .presentation-decoration {
    position: relative;
    width: 4.167vw;
    height: 4.167vw;


    .arrow {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 0.781vw;
      height: 0.781vw;
    }

    .circle {
      width: 100%;
      height: auto;
      animation: rotate360 20s linear infinite;
    }

    @keyframes rotate360 {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
  }

  .presentation_video {
    width: 100%;
    height: auto;
  }
}

.cta {
  padding: 7.292vw 0;

  .wrapper {
    display: grid;
    grid-template-columns: 23.958vw 1fr;
  }

  .cta_left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;

