  }

  .who-we_right {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 2.604vw;
  }

  .who-we_right-title {
    color: #FFF;
    font-family: Manrope;
    font-size: 1.667vw;
    font-style: normal;
    font-weight: 500;
