    flex-direction: column;
    gap: 8.333vw;

    h2 {
      color: #FFF;
      font-family: Manrope;
      font-size: 3.333vw;
      font-style: normal;
      font-weight: 500;
      line-height: 100%;
      letter-spacing: -0.167vw;
      text-transform: uppercase;
