  &:not(.no-underline):hover::before {
    transform-origin: 0% 50%;
    transform: scale3d(0, 1, 1);
  }

  &:not(.no-underline)::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 0.078vw;
    bottom: 0;
    right: 0;
    pointer-events: none;
    transform-origin: 100% 50%;
    transform: scale3d(1, 1, 1);
    transition: transform 0.3s;
    background: white;
  }
}

